@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Marck+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Montserrat', sans-serif;
}

.App {
  text-align: center;
}

.title{
font-family: 'Marck Script', cursive;
background-image: -webkit-gradient(linear, right top, left top, from(rgba(194, 212, 208, 0.904)),to(rgba(189, 220, 230, 0.815)));
background-image: -webkit-linear-gradient(right, rgba(194, 212, 208, 0.904),rgba(189, 220, 230, 0.815));
background-image: linear-gradient(-90deg, rgba(194, 212, 208, 0.904),rgba(189, 220, 230, 0.815));
padding: 20px;
text-align: center;
font-size: 50px;
width: 100%;
color: white;
text-shadow: 2px 2px 10px rgb(255, 255, 255);

}



.results{
  text-align: center;
  line-height: 30px;
  width: 300px;
  height: 400px;
  margin: 50px auto 30px auto;
  overflow: scroll;
  padding: 10px;

  border: black 1px solid;
  border-radius: 5px;
  box-shadow: 8px 8px 8px rgb(180, 180, 180);
}

.search{
  margin: 40px auto 0 auto;
  text-align: center; 
 }

 input{
  border: black 1px solid;
  padding: 5px;
}



::-webkit-scrollbar {
  width: 10px;
  height: 0px;
 
}

::-webkit-scrollbar-track {
  background: #e4e4e4; 
  border-radius: 10px;
}
 

::-webkit-scrollbar-thumb {
  background:rgb(168, 168, 168);

  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(148, 147, 147); 
}
